
@import url("https://fonts.googleapis.com/css?family=Proza+Libre");
#HDataPred{height:50vh;display:-ms-flexbox;display:flex;cursor:pointer
}
#HGeneSol{height:50vh;display:-ms-flexbox;display:flex
}
#HDataPred>.mdl-cell>.background{background-image:url("../assets/gears.jpg");background-size:cover;background-position:center;height:50%;opacity:1;z-index:0;position:absolute;width:50%;transition:opacity 0.8s
}
#HGeneSol .background{background-image:url("../assets/DNA.jpg");background-size:cover;background-position:center;height:50%;opacity:1;z-index:0;position:absolute;width:50%;transition:opacity 0.8s
}
#HDataPred>.mdl-cell{background-color:#1d1716;display:-ms-flexbox;display:flex
}
#HGeneSol>.mdl-cell{background-color:#413042;display:-ms-flexbox;display:flex
}
.product-name{font-family:'Proza Libre', serif;color:white;transition:all 0.8s ease;width:80%;margin:auto;z-index:2
}
#HDataPred .background:hover,#HGeneSol .background:hover{opacity:0.2
}
#HDataPred .background:hover+.product-name{opacity:1
}
#HGeneSol .background:hover+.product-name{opacity:1
}



