
@import url(../node_modules/animate.css/animate.css);
@import url("https://fonts.googleapis.com/css?family=Libre+Baskerville:700");
@import url("https://fonts.googleapis.com/css?family=Raleway:400");
.mdl-layout__header{background:transparent;box-shadow:0 0 0 0;animation-delay:2s;animation-duration:4s;position:fixed
}
.mdl-layout__header .mdl-layout__header-row{padding:0 100px 0 100px
}
.mdl-layout-title{font-family:'Raleway'
}
.mdl-layout-content{padding:0px
}
.mdl-cell.mdl-cell--6-col{width:calc(50%);margin:0px
}
.mdl-grid{padding:0px
}
.arrow-down{border-style:solid;border-width:15px 0px 15px 25px;border-color:transparent transparent transparent rgba(255,235,50,0.6)
}
.scroll-down{position:absolute;left:calc(50% - 9px);top:90%;color:white;font-size:18px;animation-delay:4s
}
.scroll-down>i{display:block
}
.scroll-down>i:nth-child(1){animation-duration:1.4s;animation-delay:0s;animation-iteration-count:infinite
}
.scroll-down>i:nth-child(2){animation-duration:1.4s;animation-delay:0.5s;animation-iteration-count:infinite
}
.scroll-down>i:nth-child(3){animation-duration:1.4s;animation-delay:1s;animation-iteration-count:infinite
}
.scroll-down>i:nth-child(4){animation-duration:1.4s;animation-delay:1.5s;animation-iteration-count:infinit;font-size:20px
}
.scroll-down>i:nth-child(5){animation-duration:1.4s;animation-delay:2s;animation-iteration-count:infinite;font-size:22px
}
.scroll-down>i:nth-child(6){animation-duration:1.4s;animation-delay:2.5s;animation-iteration-count:infinite;font-size:24px
}
.scroll-down>i:nth-child(7){animation-duration:1.4s;animation-delay:3s;animation-iteration-count:infinite;font-size:26px
}
.scroll-down>i:nth-child(8){animation-duration:1.4s;animation-delay:3.5s;animation-iteration-count:infinite;font-size:28px
}
.scroll-down>i:nth-child(9){animation-duration:1.4s;animation-delay:4s;animation-iteration-count:infinite;font-size:30px
}
.scroll-down>i:nth-child(10){animation-duration:1.4s;animation-delay:4.5s;animation-iteration-count:infinite;font-size:32px
}
.scroll-down>i:nth-child(11){animation-duration:1.4s;animation-delay:5s;animation-iteration-count:infinite;font-size:34px
}
#abstract{position:absolute;width:64%;right:-18%;top:57%;animation-duration:2s
}



